.links-container{
		background: #fffefa;
		margin:0;
		padding:0;
		display:-ms-flexbox;
		display:flex;
		text-decoration: none;
		list-style: none;
		-ms-flex-wrap: wrap;
		    flex-wrap: wrap;
		border:solid 1px #d6d6d6;
}


.links-container>a{
	text-decoration: none;
	color:black;
	margin:0;
	padding:0;
	text-align: center;
	-ms-flex-preferred-size: 50%;
	    flex-basis: 50%;
}
.links-container>a:hover{
	color:grey;
	cursor:pointer;
}

@media screen and (min-width:575px){
	.links-container{
		border:none;
		-ms-flex-wrap: nowrap;
		    flex-wrap: nowrap;
		font-size: 0.25rem;
		-ms-flex-item-align: end;
		    align-self: flex-end;
	}
}



