.tab-container{
	height:60px;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	background:#fffefa;
	border-top: 1px solid #d6d6d6;
	border-bottom: 3px solid #d6d6d6;
	-webkit-transition:border-bottom-color 0.2s;
	-o-transition:border-bottom-color 0.2s;
	transition:border-bottom-color 0.2s;
	position:relative;
	-webkit-transform: translateZ(0);
	        transform: translateZ(0);
}

.tab{
	display:-ms-flexbox;
	display:flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	height:100%;
	width:100%;
}
.tab-projects{
	border-right: 1px solid #d6d6d6;
}
.tab-links{
	border-left: 1px solid #d6d6d6;
}
.tab-text{
	font-size: 0.8em;
	margin-top:5px;
	color: #595959;
	-webkit-transition:-webkit-transform 0.2s;
	transition:-webkit-transform 0.2s;
	-o-transition:transform 0.2s;
	transition:transform 0.2s;
	transition:transform 0.2s, -webkit-transform 0.2s;
}
.triangle{
	opacity:0;
	width:1px;
	height:1px;
  border-style: solid;
  border-width: 10px 20px 0 20px;
  border-color: #6980fe transparent transparent transparent;
	position:absolute;
	-webkit-transition:opacity 0.3s, border-top-color 0.2s, -webkit-transform 0.3s cubic-bezier(.14,1.73,.09,1);
	transition:opacity 0.3s, border-top-color 0.2s, -webkit-transform 0.3s cubic-bezier(.14,1.73,.09,1);
	-o-transition:opacity 0.3s, transform 0.3s cubic-bezier(.14,1.73,.09,1), border-top-color 0.2s;
	transition:opacity 0.3s, transform 0.3s cubic-bezier(.14,1.73,.09,1), border-top-color 0.2s;
	transition:opacity 0.3s, transform 0.3s cubic-bezier(.14,1.73,.09,1), border-top-color 0.2s, -webkit-transform 0.3s cubic-bezier(.14,1.73,.09,1);
	z-index:3;
	-webkit-transform: translateZ(0);
	        transform: translateZ(0);
}



