.about-me-paragraph{
	background:#fffefa;
	padding:30px;
	font-size: 1.1em;
	width:100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.avatar{
	display:block;
	max-width:85%;
	border-radius: 50%;
	margin:0 auto;
	-webkit-transition: max-width 0.3s;
	-o-transition: max-width 0.3s;
	transition: max-width 0.3s;
}

@media screen and (min-width:420px){
	.avatar{
		max-width:245px;
	}
}



