.project-single-container{
	display:-ms-flexbox;
	display:flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	background-color: #fffefa;
	border:solid 1px #d6d6d6;

}

.project-name{
	text-align: left;
	margin-top:0.5em;
	font-weight:400;
	font-size: 1.8em;
}

.project-image{
	margin:10px;
	-webkit-box-shadow: 0 0 2px 5px #d6d6d6;
	        box-shadow: 0 0 2px 5px #d6d6d6;
	max-width:100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.project-title{
	font-size:1.3em;
	margin:10px 10px 0 10px;
	text-align: center;
	font-weight: 600;
}

.project-tools{
	text-align: center;
	font-size:1.2em;
	padding:0 20px;
	margin-bottom:20px;
}

.project-description{
	font-size:1.2em;
	padding:10px;
	margin:5px 10px;
}
.project-url{
	margin:20px;
	margin-top:10px;
	text-align: center;
}

.project-information>svg{
	display:block;
	margin:0 auto;
}

@media screen and (min-width:575px){
	.project-image{
		border:none;
		width:25%;
		-webkit-box-shadow: none;
		        box-shadow: none;
		margin-left:25px;
	}
	.project-single-container{
		-ms-flex-direction: row;
		    flex-direction: row;
		-ms-flex-wrap: wrap;
		    flex-wrap: wrap;
		-ms-flex-pack: distribute;
		    justify-content: space-around;
		-ms-flex-align: start;
		    align-items: flex-start;
		border:none;
	}
	.project-name{
		width:100%;
		font-size: 2em;
		margin-bottom:15px;
	}
	.project-information{
		-ms-flex:1 1;
		    flex:1 1;
		display:-ms-flexbox;
		display:flex;
		-ms-flex-direction:column;
		    flex-direction:column;
		margin-right:25px;
	}
}

@media screen and (min-width:880px){
	.project-name-tag{
		font-size: 0.8em;

	}
}



