header{
	display:-ms-flexbox;
	display:flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	height:80px;
	background:#fffefa;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}
.name-box{
	display:-ms-flexbox;
	display:flex;
	font-weight: 700;
	font-size: 1.8em;
}

.back-button{
	position:fixed;
	font-size:2.5em;
	font-weight: 700;
	-webkit-transition:all 0.2s;
	-o-transition:all 0.2s;
	transition:all 0.2s;
}
.role-box{
	display:-ms-flexbox;
	display:flex;
	font-weight: 400;
	font-size: 1.8em;
}

.name{
	font-weight: 700;
	font-size: 1.6rem;
	-webkit-transform: scale(1,1) rotate(0deg) translate(0px,0px);
	    -ms-transform: scale(1,1) rotate(0deg) translate(0px,0px);
	        transform: scale(1,1) rotate(0deg) translate(0px,0px);
	-webkit-transition: opacity 0.4s, -webkit-transform 3s;
	transition: opacity 0.4s, -webkit-transform 3s;
	-o-transition: opacity 0.4s, transform 3s;
	transition: opacity 0.4s, transform 3s;
	transition: opacity 0.4s, transform 3s, -webkit-transform 3s;
}

.role{
	font-weight: 400;
	font-size: 1.4rem;
	-webkit-transform: scale(1,1) rotate(0deg) translate(0px,0px);
	    -ms-transform: scale(1,1) rotate(0deg) translate(0px,0px);
	        transform: scale(1,1) rotate(0deg) translate(0px,0px);
	-webkit-transition: opacity 0.3s, -webkit-transform 3s;
	transition: opacity 0.3s, -webkit-transform 3s;
	-o-transition: opacity 0.3s, transform 3s;
	transition: opacity 0.3s, transform 3s;
	transition: opacity 0.3s, transform 3s, -webkit-transform 3s;

}

header>span:hover{
	cursor:pointer;
	color:grey;
}

@media screen and (min-width:575px){
	header{
		-ms-flex-align: start;
		    align-items: flex-start;
		border-bottom: solid 1px black;
		padding:25px;
		height:auto;
		padding-bottom:10px;
		padding-top:10px;
		margin-bottom:0;
		-ms-flex-direction: row;
		    flex-direction: row;
	}

	.header-left{
		display:-ms-flexbox;
		display:flex;
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex:1 1;
		    flex:1 1;
		width:70%;
		height:80px;
		-ms-flex-align: start;
		    align-items: flex-start;
		-ms-flex-pack: distribute;
		    justify-content: space-around;
	}
	.header-left>span:hover{
		cursor:pointer;
		color:grey;
	}

	.name{
		font-weight: 700;
		font-size: 1.8rem;
	}

	.role{
		font-size:1.8rem;
	}
	.header-right{
		height:80px;
		max-width:30%;
		font-size: 1.5em;
		display:-ms-flexbox;
		display:flex;
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-pack: distribute;
		    justify-content: space-around;
		-ms-flex-align: end;
		    align-items: flex-end;
	}
	.about-me-link{
		cursor:pointer;
	}
	.about-me-link:hover{
		color:grey;
	}
}

@media screen and (min-width:880px){
	header{
		margin-left:75px;
		margin-right:75px;
		padding:18.75px 0;
	}
}



